* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.ticker {
  position: relative;
  overflow: hidden;
  border-top: 1px solid var(--dark-color);
  border-bottom: 1px solid var(--dark-color);

}

.ticker__wrapper {
  display: flex;
}

.ticker__item {
  display: flex;
  flex-shrink: 0;
  padding: 0 29px;
  align-items: center;
  white-space: nowrap;
  animation: ticker 5s linear infinite;
}

.ticker__icon {
  margin-left: 29px;
}

@keyframes ticker {
  0% {
    transform: translateZ(0);
  }

  to {
    transform: translate3d(-100%, 0, 0);
  }
}
